  .animated-menu .bar:first-child {
    transform: rotateZ(45deg) translate(16px, -9px);
    width: 50%; }
  .animated-menu .bar:last-child {
    transform: rotateZ(-45deg) translate(16px, 9px);
    width: 50%; }

.nav-mobile {
