/* Timeline */
.timeline {
  border-left: 5px solid #ffb280;
  border-bottom-right-radius: 4px;
  border-top-right-radius: 4px;
  color: #333;
  margin: 2rem auto 2rem 4rem;
  letter-spacing: 0.7px;
  position: relative;
  line-height: 1.4em;
  padding: 50px;
  max-width: 100%; }
  .timeline .event {
    padding-bottom: 50px;
    position: relative; }
    .timeline .event:last-of-type {
      padding-bottom: 0;
      margin-bottom: 0;
      border: none; }
    .timeline .event::before, .timeline .event::after {
      position: absolute;
      display: block;
      top: 0; }
    .timeline .event::before {
      padding-left: 2rem;
      left: -217.5px;
      color: #333;
      content: attr(data-date);
      text-align: right;
      min-width: 120px;
      opacity: 0.7; }
    .timeline .event:after {
      box-shadow: 0 0 0 3px #ffb280;
      left: -57.85px;
      background: #fff;
      border-radius: 50%;
      height: 11px;
      width: 11px;
      content: '';
      top: 5px; }
  .timeline .event-info {
    text-align: justify;
    hyphens: auto; }

