.modal {
  position: absolute;
  top: 40px;
  left: 40px;
  right: 40px;
  bottom: 40px;
  background: #fff;
  border: 1px solid #ffb280;
  padding: 2rem;
  text-align: justify;
  hyphens: auto;
  letter-spacing: 1.3px;
  overflow: scroll;
  transition: all 500ms ease-in-out; }
  .modal:focus {
    outline: 1px solid #ffb280; }

.modal-title {
  text-transform: uppercase;
  margin-bottom: 2rem;
  text-align: center; }

.modal-img {
  width: 40%;
  float: left;
  margin: 0 1rem 1rem 0; }

.read-more-text {
  line-height: 1.5rem; }

.close-modal-fixed-btn {
  position: fixed;
  right: 10%; }

.modal-btn {
  background: #ffb280;
  border: none;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  float: right;
  margin-right: 1rem;
  cursor: pointer;
  transition: all 500ms ease-in-out;
  display: flex;
  justify-content: center;
  align-items: center; }
  .modal-btn:hover {
    background: #e65b00;
    transform: translateY(-2px); }
  .modal-btn i {
    color: #fff;
    vertical-align: middle;
    font-size: 2rem; }

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(255, 230, 230, 0.8);
  opacity: 0;
  transition: all 500ms ease-in-out; }

.ReactModal__Overlay--after-open {
  opacity: 1; }

.ReactModal__Overlay--before-close {
  opacity: 0; }

#hidden {
  visibility: hidden; }

@media screen and (max-width: 500px) {
  .modal {
    top: 0;
    left: 0;
    right: 0;
    bottom: 0; } }

