.hamburger-menu {
  width: 2rem;
  cursor: pointer;
  display: inline-block;
  margin-left: auto;
  @include transition;

  .bar {
    background: $lightColor;
    width: 100%;
    height: 3px;
    margin: 6px 0;
    border-radius: 50px;
    @include transition; } }

.animated-menu {
  transform: rotateZ(90deg);
  .bar {
    &:first-child {
      transform: rotateZ(45deg) translate(16px, -9px);
      width: 50%; }
    &:last-child {
      transform: rotateZ(-45deg) translate(16px, 9px);
      width: 50%; } } }
