.nav-flex {
  background: $primaryColor;
  display: flex;
  align-items: center;
  justify-content: center;
  color: $lightColor;
  padding: 0 $gutterWidth;

  .brand-name {
    font-size: 1.5rem;
    letter-spacing: 1.3px;
    font-weight: bold;
    margin-right: auto; }

  .subtitle {
    font-size: 1rem;
    opacity: 0.7;
    font-weight: normal;
    font-style: italic; }

  .nav-link {
    text-decoration: none;
    color: $lightColor;
    padding: 0.5rem;
    margin: 0 1rem;
    letter-spacing: 1.2px;
    font-size: 1.1rem;
    background: linear-gradient(to bottom, $secondaryColor 0%, $secondaryColor 100%);
    background-position: 0 100%;
    background-repeat: repeat-x;
    background-size: 4px 4px;
    @include transition;
    &:hover {
      background-size: 4px 50px;
      color: $darkColor; } } }
